.form__group {
  border: none;
  margin: 0;

  @media (min-width: $tablet-width) {
    width: 628px;
    padding-top: 65px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 110px;
  }
}

.form__group-tablet {
  @media (min-width: $tablet-width) {
    display: flex;
  }

  @media (min-width: $desktop-width) {
    padding-left: 100px;
  }
}

.form__group-full {
  @media (min-width: $desktop-width) {
    display: flex;
    gap: 80px;
  }
}

//.form__group--line

.form__title {
  font-family: $title-font;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
  text-align: left;
  color: $title-text;
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: $desktop-width) {
    font-size: 36px;
    line-height: 36px;
    padding-top: 70px;
  }
}

.form__label {
  font-family: $title-font;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-align: left;
  color: $text-color;
  display: flex;
  gap: 13px;
  margin-bottom: 14px;
  align-items: center;

  @media (min-width: $tablet-width) {
    display: block;
    font-size: 20px;
    padding-top: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
    display: block;
    font-size: 20px;
    padding-top: 20px;
  }
}

.form__input {
  min-width: 173px;
  height: 45px;
  border: 1px solid $border-color;
  font-family: $title-font;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-align: left;
  color: $text-color;
  flex-grow: 1;

  @media (min-width: $tablet-width) {
    min-width: 490px;
  }

  @media (min-width: $desktop-width) {
    min-width: 440px;
  }
}

.form__input--textarea {
  @media (min-width: $desktop-width) {
    width: 1220px;
    min-height: 175px;
  }
}

.form__group-list {
  list-style-type: none;
  margin: 0;

  @media (min-width: $tablet-width) {
    width: 628px;
    height: 196px;
    border: 1px solid $border-color;
    padding: 0;
    padding-left: 27px;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
  }
}

.form__group-optional {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    width: 533px;
    border: none;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    width: 1220px;
  }
}

.form__group-item {
  @media (min-width: $tablet-width) {
    padding-right: 50px;
  }
}

.form__control-radio {
  font-family: $title-font;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-align: left;
  color: $text-color;
}

//.form__input--email {}

//.form__input--tel {}

.form__group-wrapper {
  @media (min-width: $desktop-width) {}
}

//.form__checkbox {}

.form__request {
  width: 280px;
  border: none;
  margin-left: 20px;

  @media (min-width: $desktop-width) {
    width: 570px;
  }
}

.form__required-fields {
  font-family: $basic-font;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  text-align: center;
  color: $title-text;
  margin: 0;

  @media (min-width: $desktop-width) {
    font-size: 16px;
    padding-left: 80px;
  }
}
